import { useEffect, useState } from "react"
import moment from 'moment';
import * as backend from '../build/index.main.mjs'
import { Col, Image, Row } from "react-bootstrap"
import {
  useHistory,
  useParams,
  useLocation
} from "react-router-dom";
import Button from 'react-bootstrap/Button'
import search from '../search.svg'
const axios = require('axios')
const Auction = (props) => {
  const { stdlib } = props
  let history = useHistory()
  let location = useLocation()
  let { appId } = useParams()
  const [state, setState] = useState({
    ctc: false,
    view: null
  })
  useEffect(() => {
    if (props.acc && !state.ctc) {
      getContract()
    } else if (!state.once && state.ctc) {
      participate(state.ctc)
    }
  })
  const handleKeyPress = (event) => {
    let { key, target } = event
    let { value } = target
    let candidatePath = `/auction/${parseInt(value)}`
    if (key === 'Enter' && location.pathname !== candidatePath) {
      history.push(candidatePath)
      getContract()
    }
  }
  const somethingFromSome = (f, d) => (some) => some[1] ? f(some[1]) : d
  const formatCurrencyFromSome = (some) => somethingFromSome((some) => props.stdlib.formatCurrency(some, 4), 0)(some)
  const bigNumberToNumberFromSome = (some) => somethingFromSome((some) => props.stdlib.bigNumberToNumber(some), 0)(some)
  const getContract = async () => {
    try {
      await getAppInfo(appId)
      const ctc = props.acc.attach(backend, parseInt(appId))
      const view = await getViews(ctc)
      setState({
        ...state,
        ctc,
        view
      })
    } catch (e) {
      //alert("Oops! Application not found. It either does not exist or has been destroyed.")
    }
  }
  const participate = ((stdlib) => async (ctc) => {
    let bids = 0;
    let bidHistory = [];
    let buy = () => { }
    let close = () => { }
    const currentCTime = stdlib.bigNumberToNumber
      (await stdlib.getNetworkTime())
    const views = await ctc.getViews()
    const viewBids = bigNumberToNumberFromSome
      (await views.Auction.bids())
    const viewEndCTime = bigNumberToNumberFromSome
      (await views.Auction.endCTime())
    const viewBidHistory = (await views.Auction.bidHistory())
    var isAuctionOver = currentCTime >= viewEndCTime
    console.log({ particpate: { isAuctionOver, currentCTime, viewBids, viewEndCTime, viewBidHistory } })
    close = () => new Promise((resolve) => {
      resolve()
      setState({
        ...state,
        close: () => { },
        caughtUp: true,
        once: true,
        showLaunch: false,
        buy,
      })
    })
    return Promise.all([
      backend.Bidder(ctc, {
        ...stdlib.hasConsoleLogger,
        close,
        getBid: isAuctionOver
          ? (bid, cp, addr, secs) => ['None', null]
          : (bid, cp, addr, secs) => {
            console.log({ bid, cp, addr, secs })
            if (bid < viewBids) {
              bidHistory[bids++] = { cp, addr }
              console.log({
                getBid: {
                  bid,
                  cp
                }
              })
              return ['None', null]
            }
            return new Promise((resolve) => {
              buy = resolve
              bidHistory[bids++] = { cp, addr, secs };
              (async () =>
                setState({
                  ...state,
                  buy: resolve,
                  caughtUp: true,
                  once: true,
                  showLaunch: false,
                  bid: stdlib.bigNumberToNumber(bid),
                  currentPrice: stdlib.formatCurrency(cp, 4),
                  bidHistory,
                  view: {
                    ...(await getViews(state.ctc)),
                    buy: resolve,
                    /*
                    bidHistory: [...bidHistory].map(el => ({
                      cp: stdlib.formatCurrency(el.cp, 4),
                      addr: stdlib.formatAddress(el.addr),
                      secs: el.secs ? stdlib.bigNumberToNumber(el.secs) : 0
                    }))
                    */
                  },
                  update: setInterval(() => {
                    if (state.view.remainingCTime <= 0) return
                    (async () => {
                      let view = await getViews(state.ctc)
                      if (view.currentPrice !== stdlib.formatCurrency(cp, 4)) {
                        resolve(['None', null])
                      }
                    })()
                  }, 10000)
                }))()
            })
          },
        signal: (addr, token) => {
          console.log({
            signal: {
              addr,
              token
            }
          })
          setState({
            ...state,
            winner: stdlib.formatAddress(addr),
            pot: stdlib.formatCurrency(1, 4)
          })
        }
      })
    ])
  })(props.stdlib)
  const style = {
    welcome: {
      fontFamily: 'Roboto',
      fontSize: '20px',
      fontStyle: 'normal',
      fontWeight: '300',
      lineHeight: '23px',
      letterSpacing: '0.1em',
      textAlign: 'center'
    },
    serviceName: {
      fontFamily: 'Roboto',
      fontSize: '32px',
      fontStyle: 'normal',
      fontWeight: '900',
      lineHeight: '38px',
      letterSpacing: '0.1em',
      textAlign: 'center',
      textTransform: 'uppercase',
      marginTop: '4px'
    },
    serviceDescription: {
      fontFamily: 'Roboto',
      fontSize: '36px',
      fontStyle: 'normal',
      fontWeight: '300',
      lineHeight: '42px',
      letterSpacing: '0em',
      textAlign: 'center',
      textTransform: 'uppercase',
      marginTop: '20px'
    }
  }
  const searchBarStyle = {
    "width": "540px",
    "height": "60px",
    "background": "#FFFFFF",
    "border": "1px solid #C9C9C9",
    "boxSizing": "border-box",
    "boxShadow": "0px 8px 28px rgba(0, 0, 0, 0.10)",
    "borderRadius": "52px",
    "padding": "21px 53px"
  }
  const textLeftStyle = {
    "textAlign": "left"
  }
  const textRightStyle = {
    "textAlign": "right"
  }
  const descriptionStyle = {
    //"background": "#F9F9F9",
    "borderRadius": "4px",
    "marginTop": "3px",
    "fontFamily": "Roboto",
    "fontStyle": "normal",
    "fontWeight": "normal",
    "fontSize": "14px",
    "lineHeight": "16px",
    "padding": "14px",
    "marginBottom": "12px auto !important",
    //"width": "95%"
  }
  const labelStyle = {
    "marginTop": "16px"
  }
  const bidStyle = {
    "padding": "24px",

    "background": "#F3F3F3",

    "color": "#888888",
    "fontFamily": "Roboto",
    "fontStyle": "normal",
    "fontWeight": "normal",
    "fontSize": "12px",
    "lineHeight": "14px"
  }
  const auctionCardStyle = {
    "borderRadius": "12px",
    "overflow": "hidden",
    "background": "#FFFFFF",
    "boxShadow": "0px 2px 12px rgba(0, 0, 0, 0.1)",
  }
  const bidHistoryStyle = {
    "background": "#FFFFFF",
    "borderRadius": "4px",
    "marginTop": "4px",
    "paddingBottom": "10px"
  }
  const bidHistoryRowStyle = {
    "fontFamily": "Roboto",
    "fontSize": "12px",
    "fontStyle": "normal",
    "fontWeight": "500",
    "lineHeight": "14px",
    "letterSpacing": "0em",
    "textAlign": "left",
    "marginTop": "10px"
  }
  const currentBidStyle = {
    "background": "#E1E1E1",
    "borderRadius": "4px",
    "marginTop": "4px",
    "color": "black",
    "paddingTop": "8px",
    "paddingBottom": "8px",
    "fontFamily": "Roboto",
    "fontSize": "12px",
    "fontStyle": "normal",
    "fontWeight": "600",
    "lineHeight": "14px",
    "letterSpacing": "0em",
    "textAlign": "left",
    "overflow": "hidden"
  }
  const bidButtonStyle = {
    "background": "#CACACA !important",
    "boxShadow": "0px 2px 12px rgba(0, 0, 0, 0.1)",
    "borderRadius": "12px !important",
    "fontFamily": "Roboto",
    "fontStyle": "normal",
    "fontWeight": "normal",
    "fontSize": "14px",
    "lineHeight": "16px",
    "textAlign": "center",
    "textTransform": "uppercase",
    "height": "42px"
  }
  const largeAmountStyle = {
    "fontFamily": "Roboto",
    "fontSize": "16px",
    "fontStyle": "normal",
    "fontWeight": "700",
    "lineHeight": "19px",
    "letterSpacing": "0em",
    "textAlign": "left"
  }
  const getUrlPath = (url) =>
    ((delimeter) =>
      url.split(delimeter).slice(2).join(delimeter))
      ('/')

  const getCFIPFSUrl = (path) =>
    `https://cloudflare-ipfs.com/ipfs/${path}`

  const getCFIPFS = async (path) =>
    await axios.get(getCFIPFSUrl(path))

  const getAppInfo = async (appId) =>
    await axios.get(`https://testnet.algoexplorerapi.io/v2/applications/${appId}`)

  const getAsset = async (assetIndex) =>
    await axios.get(`https://testnet.algoexplorerapi.io/v1/asset/${assetIndex}`)

  const getAccount = async (address) =>
    await axios.get(`https://testnet.algoexplorerapi.io/v2/accounts/${address}`)

  // 
  const getAssetImage = async (assetIndex) => {
    let image
    try {
      const asset = await getAsset(assetIndex)
      const { url } = asset.data
      if (url.indexOf('ipfs') !== -1) {
        if (url.indexOf('json') !== -1) {
          // url is metadata json with relative path to image
          const maybeImage = (await getCFIPFS(getUrlPath(url))).data
          var path = require('path')
          image = [path.dirname(url), maybeImage.image].join('/')
        } else {
          if (url.indexOf('pinata') === -1) {
            // TODO url is another ipfs resource
            const maybeMetadata = (await getCFIPFS(getUrlPath(url))).data
            if (maybeMetadata.image.indexOf('ipfs') !== -1) {
              // use gateway url
              image = getCFIPFSUrl(getUrlPath(maybeMetadata.image))
            } else {
              // use url as is
              image = maybeMetadata.image
            }
          } else {
            // pinata url contains image
            image = url
          }
        }
      } else {
        // url may be gateway url
        image = url
      }
    } catch (e) {
      // use placeholder as fallback
      image = 'https://via.placeholder.com/1024'
    }
    return image
  }
  const getViews = async (ctc) => {
    const views = await ctc.getViews()
    return new Promise(async (resolve) => {
      const highestBidder = await views.Auction.highestBidder()
      const currentPrice = await views.Auction.currentPrice()
      const bids = await views.Auction.bids()
      const lastCTime = await views.Auction.lastCTime()
      const endCTime = await views.Auction.endCTime()
      const token = await views.Auction.token()
      const bidHistory = await views.Auction.bidHistory()
      const formmattedViewObject = {
        highestBidder: highestBidder[1]
          ? stdlib.formatAddress(highestBidder[1])
          : null,
        currentPrice: currentPrice[1]
          ? stdlib.formatCurrency(currentPrice[1], 4)
          : null,
        bids: bids[1]
          ? stdlib.bigNumberToNumber(bids[1])
          : null,
        lastCTime: lastCTime[1]
          ? stdlib.bigNumberToNumber(lastCTime[1])
          : null,
        endCTime: endCTime[1]
          ? stdlib.bigNumberToNumber(endCTime[1])
          : null,
        token: token[1]
          ? stdlib.bigNumberToNumber(token[1])
          : null,
        bidHistory: bidHistory[1]
          ? bidHistory[1].map(({ addr, price, secs }) => ({
            addr: stdlib.formatAddress(addr),
            price: stdlib.formatCurrency(price, 4),
            secs: stdlib.bigNumberToNumber(secs)
          }))
          : [],
      }
      const currentCTime =
        stdlib.bigNumberToNumber(await stdlib.getNetworkTime())
      const remainingCTime =
        Math.max(formmattedViewObject.endCTime - currentCTime, 0)
      const remainingSecs =
        (remainingCTime * 4.20).toFixed(2) // TODO use real block tx speed 
      const image = formmattedViewObject.token ? await getAssetImage(formmattedViewObject.token) : null
      const appId = await ctc.getInfo()
      resolve({
        ...formmattedViewObject,
        remainingCTime,
        remainingSecs,
        appId,
        image
      })
    })
  }
  const Timer = (props) => {
    const { initialSeconds = 0 } = props;
    const [seconds, setSeconds] = useState(initialSeconds);
    useEffect(() => {
      let myInterval = setInterval(() => {
        if (seconds > 0) {
          setSeconds(seconds - 1);
        }
      }, 1000)
      return () => {
        clearInterval(myInterval);
      };
    });
    return (
      <span>
        {moment().add(seconds, 'seconds').fromNow(true)}
      </span>
    )
  }
  const View = (props) => {
    useEffect(() => {
      console.log({ view: { props } })
    }, [props])
    const handleBid = () => {
      if (props.buy) {
        props.buy(['Some', stdlib.parseCurrency(String(parseInt(props.currentPrice) + 1))])
      }
    }
    //const displayHighestBidAddr = ((_addr) =>
    //  _addr === props.acc.networkAccount.addr ? `(you) ${_addr}` : _addr)(props.highestBidder)
    const bidHistory = [...props.bidHistory.slice(props.bids % 5),
    ...props.bidHistory.slice(0, props.bids % 5)]
      .filter(({ secs }) => secs > parseInt(0))
      .sort((a, b) => b.price < a.price)
    const lastBid = bidHistory.pop()
    return <Row className="mt-5" style={auctionCardStyle}>
      <Col className="d-xs-block d-md-none" style={{
        backgroundImage: `url(${props.image})`,
        backgroundPosition: 'center',
        backgroundSize: 'cover',
        backgroundRepeat: 'no-repeat',
      }} xs={12} md={3}>
        <Image src={props.image} alt="NFT" fluid />
      </Col>
      <Col className="d-none d-md-block" style={{
        backgroundImage: `url(${props.image})`,
        backgroundPosition: 'center',
        backgroundSize: 'cover',
        backgroundRepeat: 'no-repeat',
      }} xs={12} md={3}>
        &nbsp;
      </Col>
      <Col style={textLeftStyle} xs={12} md={6}>
        <Row>
          <Col style={{ "marginTop": "24px" }} xs={12}>
            <Row>
              <Col xs={7} className="auction-title">
                Item name
              </Col>
              <Col xs={5} className="auction-id align-middle">
                {props.appId && `ID: ${props.appId}`}
              </Col>
            </Row>
          </Col>
          <Col style={labelStyle} xs={12}>
            Creator/owner name
          </Col>
          <Col style={labelStyle} xs={12}>
            <span>Amount of NFTs:<strong style={{ "marginLeft": "2px" }}>-</strong></span><span style={{ "marginLeft": "24px" }}>Sale ends in<strong style={{ "marginLeft": "2px" }}><Timer initialSeconds={parseInt(props.remainingSecs)} /></strong></span>
          </Col>
          <Col style={labelStyle} xs={12}>
            Description
          </Col>
          <Col style={descriptionStyle} xs={12}>
            <p style={{
              "background": "#f9f9f9",
              "marginTop": "14px",
              "marginBottom": "14px",
              "padding": "14px"
            }}>
              It is a long established fact that a reader will be distracted by the readable content of a page when looking at its layout. The point of using Lorem Ipsum is that it has a more-or-less normal distribution of letters, as opposed to using 'Content here, content here', making it look like readable English. Many desktop publishing packages and web page editors now.
            </p>
          </Col>
        </Row>
      </Col>
      <Col style={{ ...bidStyle, ...textLeftStyle }} xs={12} md={3}>
        <Row>
          {bidHistory.length > 0 && <>
            <Col xs={12}>
              Order history</Col>
            <Col style={bidHistoryStyle} xs={12}>
              {bidHistory.map(({ addr, price, secs }, i) => <Row key={`bid${price}`} style={bidHistoryRowStyle}>
                <Col xs={6}>{addr.substr(0, 10)}...</Col>
                <Col xs={2}>{price}</Col>
                <Col style={textRightStyle} xs={4}>{secs > 0 ? moment.unix(secs).format('LTS') : '-'}</Col>
              </Row>)}
            </Col>
          </>}
          {lastBid && <>
            <Col style={labelStyle} xs={12}>
              Current winning bid</Col>
            <Col style={currentBidStyle} xs={12}>
              {(({ addr, price, secs }) =>
                <Row>
                  <Col xs={6}>{addr.substr(0, 10)}...</Col>
                  <Col xs={2}>{price}</Col>
                  <Col style={textRightStyle} xs={4}>{secs > 0 ? moment.unix(secs).format('LTS') : '-'}</Col>
                </Row>)(lastBid)}
            </Col>
          </>}
          <Col style={{ ...labelStyle, "marginTop": "25.5px" }} xs={12}>
            <Row>
              <Col style={textLeftStyle} xs={6}>
                Starting Price: <span style={largeAmountStyle}>-</span></Col>
              <Col style={textRightStyle} xs={6}>
                Current Bid: <span style={{ ...largeAmountStyle, "color": "black" }}>{props.currentPrice || "-"}</span></Col>
            </Row>
          </Col>
          <Col style={{ ...labelStyle, "marginTop": "19.5px", "padding": "0px" }} xs={12}>
            <Button className="w-100" style={bidButtonStyle} onClick={handleBid} disabled={!props.buy}>Make A Bid</Button></Col>
        </Row>
      </Col >
    </Row >
  }
  return <div id="auction">
    <div className="mt-2" style={style.serviceName}>Auction</div>
    <div style={style.serviceDescription}>&nbsp;</div>
    <div className="search">
      <input className="search-bar" style={searchBarStyle} placeholder="Search auction: Enter NFT Auction App Id" defaultValue={appId} onKeyPress={handleKeyPress} />
    </div>
    {state.view && <View {...state.view} />}
  </div>
}

export default Auction;