import React, { useEffect, useState } from "react";
import { loadStdlib } from '@reach-sh/stdlib'
import MyAlgoConnect from '@reach-sh/stdlib/ALGO_MyAlgoConnect';
import {
  HashRouter as Router,
  Switch,
  Route,
} from "react-router-dom";
import Home from './pages/Home'
import Sell from './pages/Sell'
import Auction from './pages/Auction'
import Navbar from './components/Navbar'
import Container from 'react-bootstrap/Container'
import './App.css';

const axios = require('axios');
const { REACT_APP_NETWORK_PROVIDER, REACT_APP_NETWORK } = process.env
const stdlib = loadStdlib(REACT_APP_NETWORK)
if (REACT_APP_NETWORK === 'ALGO') {
  stdlib.setWalletFallback(stdlib.walletFallback({
    providerEnv: "TestNet", MyAlgoConnect
  }));
}

const Faq = () => <div>
  Under Construction ...</div>

function App(props) {
  const [state, setState] = useState({
    once: false,
    acc: null,
    stdlib
  })
  const connectAccountUsingLocalStorage = async () => {
    let acc = null
    try {
      let addr = localStorage.getItem('addr')
      acc = await stdlib.connectAccount({addr}) 
    } catch(e) {
      console.log({e})
    }
    return acc
  }
  const connectAccountUsingDefaultAccount = async () => {
    let acc = null
    try {
      acc = await stdlib.getDefaultAccount()
      localStorage.setItem('addr', acc.networkAccount.addr)
    } catch(e) {
      console.log({e})
    }
    return acc
  }
  const connectAccount = async (method) => {
    console.log("Getting account ...")
    let acc
    switch(method) {
      case 'LocalStorage':
          acc = await connectAccountUsingLocalStorage()
          break
      default:
          acc = await connectAccountUsingDefaultAccount()
    }
    let accInfo = (await axios.get(`https://testnet.algoexplorerapi.io/v2/accounts/${acc?.networkAccount?.addr}`))?.data
    return {...acc, ...accInfo}
  }
  const disconnectAccount = () => {
    localStorage.removeItem('addr')
    setState({...state, acc: null})
  }
  useEffect(() => {
    if(!state.once) {
      (async() => {
        setState({
          ...state,
          once: true,
          acc: await connectAccount('LocalStorage')
        })
      })();
    }
  })
  const handleConnect = async () => {
    let acc = await connectAccount()
    setState({...state, acc})
  }
  const containerStyle =
  {
    "alignItems": "baseline",
    "display": "flex",
    "justifyContent": "center",
    "height": "100%",
  }
  return (
    <Router>
      <div className="App">
        <Navbar {...state} handleConnect={handleConnect} handleDisconnect={disconnectAccount} />
        <Container
          style={containerStyle}
          size="lg"
          fluid="lg">
          <Switch>
            <Route exact path="/auction">
              <Auction {...state} />
            </Route>
            <Route path="/auction/:appId">
              <Auction {...state} />
            </Route>
            <Route path="/sell">
              <Sell {...state} />
            </Route>
            <Route path="/faq">
              <Faq />
            </Route>
            <Route path="/">
              <Home />
            </Route>
          </Switch>
        </Container>
      </div>
    </Router>
  );
}

export default App;