import {
  useHistory,
  useLocation
} from "react-router-dom";
import wallet from '../wallet.svg';
import logo from '../logo.svg';
import Container from 'react-bootstrap/Container'
import Navbar from 'react-bootstrap/Navbar'
import Nav from 'react-bootstrap/Nav'
import ExitToAppIcon from '@material-ui/icons/ExitToApp';

const AccountInfo = (props) => {
  const style =
  {
    "margin": "auto"
  }
  const iconStyle = {
    "fontSize": "17px"
  }
  const getAddr = (history) =>
    [(props.acc?.networkAccount?.addr).substr(0, 6), (props.acc?.networkAccount?.addr).substr(-3)].join("...")
  return <>
    <span className="d-none d-md-inline-block" style={{ ...props.style, ...style }}>{Number(props.acc.amount / 1000000).toFixed(2)} ALGO</span>
    <span className="" style={{ ...props.style, ...style }}>{getAddr()}</span>
    <ExitToAppIcon className="d-none d-md-inline-block" role="button" onClick={props.handleDisconnect} style={{...props.style, ...style, ...iconStyle }} />
  </>
}

const NFTJamNavbar = (props) => {
  let { acc, handleConnect, stdlib } = props
  let location = useLocation()
  let history = useHistory()
  const navbarBrandNameStyle = {
    lineHeight: '62px',
    marginLeft: '8px'
  }
  const navbarBrandTypographyStyle =
  {
    "fontFamily": "Rubik Mono One",
    "fontStyle": "normal",
    "fontWeight": "normal",
    "fontSize": "22px",
    "lineHeight": "27px"
  }
  const navbarLinkTypographyStyle =
  {
    "fontFamily": "Rubik",
    "fontSize": "14px",
    "fontStyle": "normal",
    "fontWeight": "700",
    "lineHeight": "17px",
    "letterSpacing": "0em",
    "textAlign": "center"
  }
  const navbarLinkStyle =
  {
    "color": "white",
    "textAlign": "center",
    "textTransform": "uppercase"
  }
  const navbarLinkWalletConnectStyle =
  {
    "display": "flex",
    "alignItems": "center"
  }
  const navbarLinkWalletConnectIconStyle =
  {
    "top": "-5px",
    "filter": "invert(1)",
    "height": "17px"
  }
  const navbarLinkWalletConnectLabelStyle =
  {
    "marginLeft": "7px"
  }
  const navbarStyle =
  {
    "padding": "26px",
    "color": "white"
  }
  const navbarCollapseStyle = {
    "justifyContent": "flex-end",
  }
  return <Navbar sticky="top" style={navbarStyle}>
    <Container>
      <Navbar.Brand className="text-light" onClick={() => history.push("/")}>
        <img
          src={logo}
          width="33"
          height="42"
          className="d-inline-block"
          alt="NFT Jam Logo"
        />
        <div className="d-inline-block" style={{ ...navbarBrandNameStyle, ...navbarBrandTypographyStyle }}>
          NFT Jam
        </div>
      </Navbar.Brand>
      <Navbar.Toggle aria-controls="basic-navbar-nav" />
      <Navbar.Collapse id="basic-navbar-nav" style={navbarCollapseStyle}>
        <Nav style={{ columnGap: '2em', fontWeight: '500' }}>
          {[
            {
              path: "/sell",
              text: "Sell NFT",
              className: location.pathname.indexOf('sell') !== -1 ? "active" : ""
            },
            {
              path: "/auction",
              text: "Auction",
              className: location.pathname.indexOf('auction') !== -1 ? "active" : ""
            },
            {
              path: "/faq",
              text: "FAQ",
              className: location.pathname.indexOf('faq') !== -1 ? "active" : ""
            },
          ].map(el =>
            <Nav.Link style={{ ...navbarLinkStyle, ...navbarLinkTypographyStyle }} className={[el.className, "d-none d-md-block"].join(" ")} onClick={() => history.push(el.path)}>{el.text}</Nav.Link>
          )}
          {acc ? (
            <AccountInfo {...props} style={navbarLinkTypographyStyle} />
          ) : (
            <span role="button" onClick={handleConnect} style={{ ...navbarLinkStyle, ...navbarLinkTypographyStyle, ...navbarLinkWalletConnectStyle }}><img src={wallet} style={{ ...navbarLinkWalletConnectIconStyle }} alt="Algorand Wallet Icon" width="30" height="26" /><span style={{ ...navbarLinkWalletConnectLabelStyle }}>Connect <span className={"d-none d-md-inline-block"}>wallet</span></span></span>
          )}
        </Nav>
      </Navbar.Collapse>
    </Container>
  </Navbar>
}

export default NFTJamNavbar;
